<template>
    <div>
        <div class="col-md-11 p-2 form-row  mb-4 ml-5">
            <div class="col-md-1"><img src="@/assets/images/icons/user.png" class="mr-2 mt-5" /></div>
            <div class="col-md-11" :class="{'form-group--error':$v.comments.$error}">
                <p>Comments</p>
                <textarea v-model="$v.comments.$model" class="form-control mb-2" rows="4"></textarea>
                <div v-if="commentError">
                    <span class="text-danger" v-if="!$v.comments.required"> Please provide the comments</span>
                </div>
            </div>
        </div>

        <div class="comment-buttons">
            <button class="btn btn-primary mb-3 pl-5 mr-2 pr-5" style="float:right;" @click="Approve">Approve</button>
            <button class="btn btn-outline-primary mb-3 pl-5 pr-5 mr-2 p-2" style="float:right;" @click="Reject">Reject</button>
            <button class="btn btn-outline-primary mb-3 mr-2 p-2 pl-5 pr-5" style="float:right;" @click="redirectToHome"><i class="fa fa-arrow-left"></i> Back</button>
        </div>
    </div>
</template>
<script>
    import axios from 'axios';
    import toast from '@/toast';
    import { required } from 'vuelidate/lib/validators';
    export default {
        data() {
            return {
                comments: '',
                commentError: false
            }
        },
        validations: {
            comments: { required }
        },
        props: ['orderId'],
        methods: {
            Approve: function () {
                axios.post(process.env.VUE_APP_OrderMgmtApisBaseUrl + "/Orders/ApproveOrder", { orderId: this.orderId, comments: this.comments }).then(() => {
                    toast.success("Approved!..");
                    this.redirectToHome();
                })
            },
            Reject: function () {
                this.$v.$touch();
                this.commentError = this.$v.comments.$anyError;
                if (!this.commentError) {
                    axios.post(process.env.VUE_APP_OrderMgmtApisBaseUrl + "/Orders/RejectOrder", { orderId: this.orderId, comments: this.comments }).then(() => {
                        toast.success("Rejected!..");
                        this.redirectToHome();
                    })
                }
            },
            redirectToHome() {
                this.$router.go(-1)
            }
        }
    }
</script>